@import url('https://fonts.googleapis.com/css?family=Open+Sans');

.developers {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 1%;
}

.headshot {
  width: 100%;
  max-width: 256px;
  max-height: 256px;
  border-radius: 50%;
  -webkit-transition: border-radius .1s ease-in;
  -o-transition: border-radius .1s ease-in;
  transition: border-radius .1s ease-in;
}

.developer:hover .headshot {
  border-radius: 2.5%;
  -webkit-transition: border-radius .5s ease-out;
  -o-transition: border-radius .5s ease-out;
  transition: border-radius .5s ease-out;
}

.title {
  font-weight: 100;
}

.developer {
  margin-left: auto;
  margin-right: auto;
  padding: 1%;
  text-align: center;
  color: white;
  width: 384px;
}

.developer:hover .bio {
  color: white;
  -webkit-transition: color .5s;
  -o-transition: color .5s;
  transition: color .5s;
}

.bio {
  word-wrap: normal;
  color: transparent;
}

.developer:hover .key,
.developer:hover .email {
  color: white;
  -webkit-transition: color .5s;
  -o-transition: color .5s;
  transition: color .5s;
}
.key,
.email {
  color: transparent;
  padding: 5px;
  font-size: 150%;
}

.developer hr {
  width: 0px;
  visibility: hidden;
}

.developer:hover hr {
  width: 100%;
  visibility: visible;
  -webkit-transition: width .5s;
  -o-transition: width .5s;
  transition: width .5s;
}

#logo {
  width: 128px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

h1 {
  text-align: center;
  color: white;
}

#logo:hover {
  -webkit-animation-name: showTitle;
          animation-name: showTitle;
}

body {
  background: -webkit-linear-gradient(309deg, rgb(67, 90, 158), #283C86 30%, #4E4E8A 48%, #379B9B 65%, #41B774 85%, #45A247);
  background: -o-linear-gradient(309deg, rgb(67, 90, 158), #283C86 30%, #4E4E8A 48%, #379B9B 65%, #41B774 85%, #45A247);
  background: linear-gradient(141deg, rgb(67, 90, 158), #283C86 30%, #4E4E8A 48%, #379B9B 65%, #41B774 85%, #45A247);
  font-family: 'Open Sans', sans-serif;
}


